.header_mobile-block {
  height: 50px;
  background-color: #061e37;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: none;
  padding: 0 10px 0 10px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
}

.burger {
  width: 30px;
  height: 24px;
  cursor: pointer;
  position: relative;
  margin: 0 10px 0 0;
}

.burger__line {
  display: block;
  width: 100%;
  height: 4px;
  background-color: #fff;
  position: absolute;
  left: 0;
  transition: 0.3s all linear;
}

.burger__line_first {
  top: 0;
}

.burger__line_second,
.burger__line_third {
  top: 50%;
  transform: translateY(-50%);
}

.burger__line_fourth {
  bottom: 0;
}

.burger_active .burger__line_first,
.burget_active .burger__line_fourth {
  opacity: 0;
}

.burger_active .burger__line_fourth {
  opacity: 0;
}

.burger_active .burger__line_second {
  transform: translateY(-50%) rotate(45deg);
}

.burger_active .burger__line_third {
  transform: translateY(-50%) rotate(-45deg);
}

