}

.contact__input-text:nth-child(2n) {
  margin: 0 0 0 40px;
}

.contact__textarea {
  border: 1px solid #061e37;
  border-radius: 5px;
  padding: 15px 20px 15px 20px;
  width: 100%;
  box-sizing: border-box;
  resize: none;
  margin: 0 0 20px 0;
  background-color: #fafafa;
  font-size: 14px;
  line-height: 30px;
  color: #061e37;
  font-weight: 500;
  font-family: "Raleway";
  height: 150px;
}

.contact__input-checkbox {
  font-size: 20px;
  color: #061e37;
  text-align: center;
  margin: 20px 0 0 0;
}

.contact__checkbox-text {
  font-size: 15px;
  color: #061e37;
  font-weight: 500;
  font-family: "Raleway";
}

.contact__button {
  @include button_family;
  color: #fff;
  background-color: #1de9b6;
  padding: 18px 45px 18px 45px;
}

.footer-container {
  padding: 90px 80px 110px 80px;
  background-color: #061e37;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.footer__info_list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  list-style-type: disc;
  margin: 50px 0 0 0;
}

.footer__info-item:nth-child(1){
  list-style-type: none;
  margin: 0;
}

.footer__info-item {
  font-size: 15px;
  line-height: 25px;
  color: #ffffff;
  font-weight: 400;
  font-family: "Raleway";
  margin: 0 0 0 45px;
}

.footer__social_links{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin: 60px 0 0 0;
}

.footer__social-item{
  margin: 0 20px 0 0;
}

.footer__social-item:nth-last-child(1){
  margin: 0;
}

.navigation_list_footer{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
// Hover effects

.navigation__item_footer:hover{
  cursor: pointer;
}

.header-block__link:hover,
.navigation__item:hover {
  @include hover-effect;
}

.advantages__button:hover {
  background-color: #fff;
  color: #1de9b6;
}

.contact__button:hover {
  background-color: #fafafa;
  color: #1de9b6;
}
.customers__button:hover {
  color: #061e37;
  background-color: #fff;
}

.introduction__button-try:hover {
  background-color: $hover-color;
  color: #fff;
}

.introduction__button-buy:hover {
  background-color: #fff;
  color: $hover-color;
}

.introduction-video__link:hover {
  background-color: #fff;
  width: 65px;
  height: 65px;
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.2);
}

.our_team__member-image:hover {
  cursor: pointer;
}

.service:hover {
  cursor: pointer;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.13);
}
button {
  cursor: pointer;
}

.introduction-video__link:hover .link-arrow {
  color: $hover-color;
}

.menu_active {
  visibility: visible;
  top: 50px;
  transition: 0.3s;
}

// Media requests

@media (max-width: 1300px) {
  .introduction {
    flex-direction: column;
    justify-content: center;
    padding: 0 30px 0 30px;
  }

  .introduction__text-block {
    width: 100%;
    align-items: center;
  }

  .introduction-video {
    width: 80%;
    margin: 100px 0 0 0;
  }

  .introduction__header-text {
    text-align: center;
  }

  .customers {
    flex-direction: column;
  }

  .customers__header-block {
    width: 100%;
    margin: 0 0 40px 0;
  }

  .customers__header-text,
  .customres__text {
    text-align: center;
  }

  .customers_slider-desktop {
    display: none;
    overflow-y: hidden;
  }

  .customers_slider-mobile {
    margin: 0;
    width: 100%;
    padding: 0 30px 0 30px;
    display: block;
  }

  .customers_slider-mobile .slick-track {
    display: flex;
  }

  .customers_slider-mobile .slider__item {
    width: 100% !important;
    height: auto;
    margin: 0;
    justify-content: center;
    flex-direction: column;
  }

  .customers_slider-mobile .slick-arrow {
    top: 60% !important;
    width: 15px;
    height: 23px;
  }

  .customers_slider-mobile .slick-arrow.slick-prev {
    left: 0;
    background-image: url("../../images/arrow_left.png");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }

  .customers_slider-mobile .slick-arrow.slick-next {
    left: auto;
    right: 15px !important;
    background-image: url("../../images/arrow_right.png");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }

  .review {
    margin: 0;
    width: 100%;
  }

  .customers__header-block {
    align-items: center;
  }

  .customers__text {
    margin: 25px 0 40px 0;
    text-align: center;
  }
}

@media (max-width: 1000px) {
  .header_mobile-block {
    display: flex;
  }

  .header-block {
    display: none;
  }

  .introduction-container {
    padding: 0 0 90px 0;
  }

  .introduction {
    padding: 50px 30px 0 30px;
    margin: 50px 0 0 0;
  }

  .advantages__header-block {
    width: 90%;
  }
  .advantage {
    flex-basis: 30%;
  }
  .achievement-container {
    width: 50%;
  }
  
  .footer-container{
    padding: 20px 15px 20px 15px;
  }

  .footer{
    flex-direction: column;
  }

  .footer__dart_info,
  .footer__info_list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .footer__social_links{
    justify-content: center;
  }
  .footer__dart_info{
    margin: 0 0 15px 0;
  }
  .footer__info-item:nth-child(1){
    list-style-type: disc;
    margin: 0 0 0 45px;
  }
}

@media (max-width: 900px) {
  .services {
    width: 100%;
  }
  .services-container {
    padding: 70px 15px 65px 15px;
  }
}
@media (max-width: 700px) {
  .advantages__header-block {
    width: 100%;
    padding: 0 15px 0 15px;
  }
  .advantages {
    padding: 0 15px 0 15px;
    margin: 52px 0 70px 0;
    flex-wrap: wrap;
  }
  .advantage {
    width: 100%;
    margin: 30px 0 0 0;
    flex-basis: auto;
  }
  .advantages-container {
    padding: 95px 15px 95px 15px;
  }

  .achievement-container {
    width: 100%;
  }

  .services__drop_down {
    flex-direction: column;
  }

  .services__dropdown-text {
    margin: 15px 0 0 0;
  }

  .our_team {
    width: 100%;
  }

  .our_team__member-image {
    width: 60px;
    height: 60px;
  }
  .member_image-active {
    width: 100px !important;
    height: 100px !important;
    border: 14px solid #ffffff;
  }
  .our_team__header-block {
    margin: 0 0 40px 0;
  }
  .our_team__chooser {
    height: 100px;
  }
  .contact-container {
    padding: 30px 15px 30px 15px;
  }
  .contact {
    width: 100%;
  }
  .contact__input-text {
    width: 100%;
    margin: 0 0 20px 0 !important;
  }
}

@media (max-width: 500px) {
  .introduction__buttons {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 0 0 0;
  }

  .introduction__button-try {
    margin: 15px 0 0 0;
  }

  .introduction-video {
    width: 100%;
  }

  .achievement_text {
    font-size: 16px;
  }
  .customers-container {
    padding: 38px 25px 38px 25px;
  }
  .review {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .customer__name {
    margin: 0 0 15px 0;
  }
  .customers__text {
    text-align: center;
  }
  .navigation_list_footer{
    display: none;
  }
}

@media (max-width: 400px) {
  .contact__form {
    flex-direction: column;
  }
  .contact__button {
    margin: 20px 0 0 0;
  }
}

// IE
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .member_image-active {
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.15);
  }
}
